html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat Alternates", sans-serif;
  font-size: 1.6rem; }

p {
  font-size: 2rem;
  color: #000; }

.content {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none;
  font-weight: bold; }

img {
  max-width: 100%; }

h1, h2, h3 {
  margin: 0; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.header {
  background-color: #42AB49;
  transition: .3s ease-in-out; }
  .header .content-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 0; }
    @media (min-width: 768px) {
      .header .content-header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center; } }
    .header .content-header h1 {
      color: #fff;
      margin-bottom: 2rem; }
      @media (min-width: 768px) {
        .header .content-header h1 {
          margin-bottom: 0; } }
  .header.fixed {
    position: fixed;
    width: 100%;
    z-index: 2;
    box-shadow: 0px 10px 24px 0px rgba(0, 0, 0, 0.55); }

.main-nav a {
  color: #fff;
  font-size: 2rem;
  margin-right: 2rem;
  transition: 0.2s ease-in-out; }
  .main-nav a:last-of-type {
    margin-right: 0; }
  .main-nav a:hover {
    color: #F1C40F; }

.video {
  position: relative;
  max-height: 60rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(128, 22, 160, 0.411); }
  .video video {
    display: block;
    width: 100%; }

.content-video {
  display: flex;
  height: 100%;
  align-items: center;
  position: relative;
  z-index: 1; }
  .content-video h2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: #fff;
    font-size: 4rem; }
    @media (min-width: 480px) {
      .content-video h2 {
        font-size: 7rem; } }
    .content-video h2 span {
      background-color: #D90368;
      font-size: 2rem;
      padding: 1rem 2rem; }

.about-festival {
  padding: 5rem 0; }
  @media (min-width: 768px) {
    .about-festival {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 2rem; } }

.text-content {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .text-content h2 {
    font-size: 3rem; }
  .text-content .date {
    color: #D90368;
    font-weight: bold; }
  .text-content .text {
    line-height: 1.5; }

.lineup {
  background-color: #D90368;
  box-shadow: -5px -13px 19px -17px rgba(0, 0, 0, 0.75);
  padding: 5rem 0; }
  .lineup h3,
  .lineup p {
    text-align: center;
    color: #fff; }
  .lineup .dia {
    text-transform: uppercase;
    margin: 5rem 0; }

.stage-content .stage {
  border-radius: 2rem; }

@media (min-width: 768px) {
  .stage-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 5rem; } }

.stage-content .name-stage {
  color: #000;
  font-size: 2rem; }
  .stage-content .name-stage::after {
    content: "";
    width: 20rem;
    height: 10rem;
    display: block;
    margin: 2rem auto 0 auto;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center; }

.edm .name-stage::after {
  background-image: url(/build//img/icono_edm.png); }

.rock .name-stage::after {
  background-image: url(/build/img/icono_rock.png); }

.yellow {
  background-color: #F1C40F; }

.blue {
  background-color: #2274A5; }

.schedule {
  list-style: none;
  padding: 0; }
  .schedule li {
    padding: 2rem;
    font-weight: bold; }
    .schedule li span {
      font-weight: normal; }
    .schedule li:nth-child(odd) {
      background-color: rgba(0, 0, 0, 0.15); }

.gallery {
  padding: 5rem 0; }
  .gallery h3 {
    text-align: center; }

.image-gallery {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  align-content: center;
  margin-top: 5rem;
  list-style: none;
  padding: 0; }
  @media (min-width: 480px) {
    .image-gallery {
      grid-template-columns: repeat(3, 1fr); } }
  @media (min-width: 768px) {
    .image-gallery {
      grid-template-columns: repeat(4, 1fr); } }
  @media (min-width: 1200px) {
    .image-gallery {
      grid-template-columns: repeat(6, 1fr); } }
  .image-gallery img {
    cursor: pointer;
    transition: .2s ease-in-out; }
    .image-gallery img:hover {
      filter: drop-shadow(2px 4px 6px gray) brightness(0.7); }

.block {
  overflow: hidden; }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.4);
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center; }
  .overlay img {
    padding: 2rem; }

.tickets {
  background-color: #2274A5;
  padding: 5rem 0; }
  .tickets h3 {
    text-align: center;
    color: #fff; }
  .tickets .content-tickets {
    margin-top: 2rem; }
    @media (min-width: 480px) {
      .tickets .content-tickets {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 5rem; } }
  .tickets .plan p {
    color: #fff;
    font-size: 3rem;
    text-align: center;
    margin-bottom: 2rem; }

.pass {
  width: 100%;
  border-spacing: 0; }
  .pass thead {
    background-color: #D90368; }
    .pass thead th {
      padding: 1rem;
      color: #fff;
      text-transform: uppercase;
      font-weight: normal;
      border-radius: 2rem 2rem 0 0; }
  .pass tbody {
    background-color: #fff;
    text-align: center; }
    .pass tbody td {
      padding: 1rem;
      border-bottom: 1px solid lightgray; }
  .pass tfoot {
    background-color: #42AB49; }
    .pass tfoot td {
      padding: 1rem;
      text-align: center;
      font-size: 3rem;
      color: #fff; }

.footer {
  background-color: #D90368;
  padding: 2rem;
  text-align: center; }
  .footer p {
    color: #fff;
    margin: 0; }
